import React, {Component} from 'react';

import { Col, Card, Icon, Avatar, Modal } from 'antd';

import imgURL_personal from '../img/personal_web.jpg';
import imgURL_weather from '../img/weather_web.jpg';
import imgURL_lms from '../img/lms-web.jpg';

export default class ProjectView extends Component{
    constructor(props){
        super(props)
        this.state=({  });
    }

    render(){
        const { Meta } = Card;
        return (
            <div>
                <Col span={4}>
                </Col>
                <Col span={16}>
                    <h1 style={{paddingTop:'40px'}}>My Projects</h1>
                    <Card
                        style={{ width: 600, marginTop:'40px' }}
                        cover={ <a href="https://samuelgunuaa.github.io/" target="_blank">
                                    <img alt="example" src={imgURL_personal} style={{width: 300, float: 'right' }}/>
                                </a> }
                    >
                        <Meta style={{ paddingTop: 25, paddingBottom: 20 }}
                            title="Personal web page"
                        />
                        <a href="https://github.com/SamuelGuNUAA/SamuelGuNUAA.github.io/tree/react-personal-page/" target="_blank"><Icon type="github"/> SPA / React.js / Ant Design</a>
                    </Card>
                    <Card
                        style={{ width: 600, marginTop:'40px' }}
                        cover={ <a href="https://samuelgunuaa.github.io/react-weather-pro/" target="_blank">
                                    <img alt="example" src={imgURL_weather} style={{width: 300, float: 'right' }}/>
                                </a> }
                    >
                        <Meta style={{ paddingTop: 25, paddingBottom: 20 }}
                            title="Weather forecast web page"
                        />
                        <a href="https://github.com/SamuelGuNUAA/react-weather-pro/" target="_blank"><Icon type="github"/> React.js / Materia UI</a>
                    </Card>
                    <Card
                        style={{ width: 600, marginTop:'40px', marginBottom:'20px' }}
                        cover={ <img alt="example" src={imgURL_lms} style={{width: 300, float: 'right' }}/>}

                    >
                        <Meta style={{ paddingTop: 25, paddingBottom: 20 }}
                            title="Learning Management System"
                            description="(Engineering development)"
                        />
                        <a href="https://github.com/SamuelGuNUAA/API-LMS-Backend/" target="_blank"><Icon type="github"/> MySQL / ASP.NET C#</a>
                        <br />
                        <a href="https://github.com/SamuelGuNUAA/App-LMS-Frontend/" target="_blank"><Icon type="github"/> React.js / Ant Design</a>
                    </Card>
                </Col>
            </div>
        )
    }
}


